/* table */
table{width:100%;}
table td{font-weight:300;}
table th{font-weight:400;}

/* clear */
.clearfix:after{clear:both;content:"";display:block}

/* hidden */
.hide{display:block;overflow:hidden;position:absolute;left:-9999px;width:1px;height:1px;font-size:0;line-height:0;text-indent:-9999px}

/* position */
.pos_r{position:relative !important;}
.pos_a{position:absolute !important;}
.pos_f{position:fixed !important;}
.pos_s{position:static !important;}

/* float */
.fl_l {float:left !important;}
.fl_c {display:inline-block; *display:inline; zoom:1;}
.fl_r {float:right !important;}

/* text-align */
.txt_l {text-align:left !important;}
.txt_c {text-align:center !important;}
.txt_r {text-align:right !important;}

/* vertical */
.vt{ vertical-align:top !important;}
.vm{ vertical-align:middle !important;}
.vb{ vertical-align:bottom !important;}

/* 가상선택자 삭제 */
.bef-none::before,.aft-none::after,.beaf-none::before,.beaf-none::after{content:none !important}

/* 컨텐츠 중앙 정렬(!aos사용시 걷어내기) */
.boxCenter{position:absolute;bottom:50%;right:50%;-webkit-transform:translate(50%, 50%);-moz-transform:translate(50%, 50%);-ms-transform:translate(50%, 50%);-o-transform:translate(50%, 50%);transform:translate(50%, 50%);width:100%;min-width:1200px;z-index:10;}

/* border 0 */
.bt0{border-top:0 !important}
.br0{border-right:0 !important}
.bb0{border-bottom:0 !important}
.bl0{border-left:0 !important}

